  font-family: var(--primary-font-family);
  margin-bottom: 20px;

  min-height: 230px;
  background-size: cover;
  background-image: url(../img/header-home2/header-img-mob.png);
  opacity: 0.9;

  @include retina {
    background-image: url(../img/header-home2/header-img-mob-2x.png);
  }

  @include tablet {
    min-height: 216px;
    background-image: url(../img/header-home2/header-img-tablet.png);

    @include retina {
      background-image: url(../img/header-home2/header-img-tablet-2x.png);
    }
  }

  @include desktop {
    background-image: url(../img/header-home2/header-img.png);

    @include retina {
      background-image: url(../img/header-home2/header-img-2x.png);
    }
  }
}

