.loader {
  height: 100%;
  width: 100%;
  z-index: 9999;
  background: #302d2d;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader .loader_inner {
  clip: rect(0, 80px, 80px, 40px);
  height: 80px;
  width: 80px;
  animation: animate 1.5s linear infinite;
  position: absolute;
  top: calc(50% - 40px);
  left: calc(50% - 40px);
}

@keyframes animate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(220deg);
  }
}

.loader .loader_inner:after {
  clip: rect(0, 80px, 80px, 40px);
  content: "";
  height: 80px;
  width: 80px;
  border-radius: 50%;
  animation: animate2 1.5s ease-in-out infinite;
  position: absolute;
}

@keyframes animate2 {
  0% {
    transform: rotate(-140deg);
    box-shadow: inset 0 0 0 17px #f7a6a6;
  }

  50% {
    box-shadow: inset 0 0 0 2px #fa6c6c;
  }

  100% {
    transform: rotate(140deg);
    box-shadow: inset 0 0 0 17px #ec0505;
  }
}

